import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './index.scss'
import MYButton from '../../components/MYButton/index.js'
export default class index extends Component {
    componentDidMount() {
        gsap.from(
            '.home',
            {
                opacity: 0,
                duration: 1,
                x: '30px',
                ease: 'slow(0.7,0.7,0.7,0.7, false)',
            },
            1
        )
    }
    render() {
        return (
            <div className="home">
                <div className="content">
                    <ul>
                        <li>:</li>
                        <li>-React-Basic</li>
                        <li>-React-Router</li>
                        <li>-React-PropType</li>
                        <li>-Webpack</li>
                        <li>
                            <MYButton
                                className={'a'}
                                to="/list"
                            ></MYButton>
                        </li>
                    </ul>
                    <ul>
                        <li>:</li>
                        <li>-AWS</li>
                        <li>-AWS-EC2</li>
                        <li>-AWS-S3</li>
                        <li>-AES-RDS</li>
                        <li>
                            <MYButton
                                className={'a'}
                                to="/board"
                            ></MYButton>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}
